import React from 'react'
import './MenuPanel.css'
import Menu from '../menu/Menu'
import MenuItem from '../menu/MenuItem'
import {observer} from "mobx-react-lite";
import {apiStore} from "../../stores/AppStore";

const MenuPanel = () => {

    return (
        <div className={'menu_panel'}>
            <Menu title={' '}>
                {apiStore.isAuth ?
                    <MenuItem onClick={() => apiStore.setOpenFormModal(true)}></MenuItem>
                    : <MenuItem> </MenuItem>}
            </Menu>
            <Menu title={''}>
                {apiStore.isAuth ? (
                    <>
                        <MenuItem onClick={() => apiStore.loadData(2)}> 1</MenuItem>
                        <MenuItem onClick={() => apiStore.loadData(3)}> 2</MenuItem>
                    </>
                ) : (
                    <MenuItem> </MenuItem>
                )}
            </Menu>
            <Menu title={''}>
                {apiStore.isAuth ? (
                    <>
                        <MenuItem onClick={apiStore.loadAbout}> </MenuItem>
                    </>
                ) : (
                    <MenuItem> </MenuItem>
                )}
            </Menu>

        </div>
    )
}

export default observer(MenuPanel)
