const BASE_API = `http://localhost:5000/`

const COUNTRIES = [
    {code: 'RU', label: '', phone: '7'},
    {code: 'BY', label: 'Belarus', phone: '375'},
    {code: 'AF', label: 'Afghanistan', phone: '93'}
]

const columns = [
    {field: 'family', headerName: '', width: 90},
    {
        field: 'name',
        headerName: '',
        width: 150,
    },
    {
        field: 'surname',
        headerName: '',
        width: 150,
    },
    {
        field: 'sex',
        headerName: '',
        width: 150,
    },
    {
        field: 'country',
        headerName: '',
        width: 150,
        valueGetter: (params) => {
            return params.label;
        }
    },
    {
        field: 'age',
        headerName: '',
        width: 150,
    },
    {
        field: 'food',
        headerName: '',
        width: 400,
        valueGetter: (params) => {
            let result = ''
            if (params?.dietician) result += '\n'
            if (params?.vegan) result += '\n'
            if (params?.traditional) result += '\n'
            return result
        }
    },
]
export {BASE_API,COUNTRIES, columns}