import React, {useEffect} from 'react';
import {apiStore} from "../../stores/AppStore";
import {
    Autocomplete, Box,
    Button, Checkbox, Dialog,
    DialogTitle,
    FormControl,
    FormControlLabel, FormGroup,
    FormLabel,
    Radio,
    RadioGroup,
    TextField
} from "@mui/material";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import {observer} from "mobx-react-lite";
import DialogActions from "@mui/material/DialogActions";
import {COUNTRIES} from "../../consts";

function Dialogs() {

    useEffect(() => {
        if (apiStore.formData.name !== '') {
            apiStore.setErrorName(false)
            return
        }
        apiStore.setErrorName(true)

    }, [apiStore.formData.name])

    // useEffect(() => {
    //     if (apiStore.login !== '') {
    //         apiStore.setErrorLogin(false)
    //         return
    //     }
    //     apiStore.setErrorLogin(true)
    // }, [apiStore.login])
    //
    // useEffect(() => {
    //     if (apiStore.password !== '') {
    //         apiStore.setErrorPassword(false)
    //         return
    //     }
    //     apiStore.setErrorPassword(true)
    // }, [apiStore.password]);


    return (
        <>
            <Dialog open={apiStore.openFormModal} onClose={() => apiStore.setOpenFormModal(false)}
            >
                <DialogTitle></DialogTitle>
                <DialogContent>
                    {/*<DialogContentText><Box>      </Box></DialogContentText>*/}
                    <Box component="section" sx={{p: 1}}>
                        <TextField fullWidth id="family" label="" variant="outlined"
                                   onChange={(event) => apiStore.onChangeFamily(event.target.value)}/>
                    </Box>

                    <Box component="section" sx={{p: 1}}>

                        <TextField fullWidth id="name" label="" variant="outlined" required
                                   error={apiStore.errorName}
                                   onChange={(event) => apiStore.onChangeName(event.target.value)}/>
                    </Box>

                    <Box component="section" sx={{p: 1}}>
                        <TextField fullWidth id="surname" label="" variant="outlined"
                                   onChange={(event) => apiStore.onChangeSurame(event.target.value)}/>
                    </Box>
                    <Box component="section" sx={{p: 1}}>
                        <FormControl>
                            <FormLabel id="demo-radio-buttons-group-label"></FormLabel>
                            <RadioGroup
                                row
                                aria-labelledby="demo-radio-buttons-group-label"
                                defaultValue="female"
                                name="radio-buttons-group"
                                value={apiStore.formData.sex}
                                onChange={(event, value) => {
                                    apiStore.onChangeSex(value)
                                }}

                            >
                                <FormControlLabel value="female" control={<Radio/>} label="."/>
                                <FormControlLabel value="male" control={<Radio/>} label="."/>
                            </RadioGroup>
                        </FormControl>
                    </Box>

                    <Box component="section" sx={{p: 1}}>
                        <Autocomplete
                            id="country-select-demo"
                            sx={{width: 300}}
                            options={COUNTRIES}
                            autoHighlight
                            getOptionLabel={(option) => option.label || ''}
                            isOptionEqualToValue={(option, value) => option.code === value.code}
                            renderOption={(props, option) => (
                                <Box component="li" sx={{'& > img': {mr: 2, flexShrink: 0}}} {...props}>
                                    <img
                                        loading="lazy"
                                        width="20"
                                        srcSet={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png 2x`}
                                        src={`https://flagcdn.com/w20/${option.code.toLowerCase()}.png`}
                                        alt=""
                                    />
                                    {option.label} ({option.code}) +{option.phone}
                                </Box>
                            )}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    label="  "
                                    inputProps={{
                                        ...params.inputProps,
                                    }}
                                />
                            )}
                            onChange={(event, value) => {
                                apiStore.onChangeCountry(value);
                            }}
                        />
                    </Box>

                    <Box component="section" sx={{p: 1}}>
                        <TextField type="number"
                                   label=" "
                                   InputProps={{
                                       inputProps: {min: 0}
                                   }}
                                   value={apiStore.formData.age}
                                   onChange={(event) => apiStore.onChangeAge(event)}
                        />
                    </Box>

                    <Box component="section" sx={{p: 1}}>
                        <FormControl sx={{m: 3}} component="fieldset" variant="standard">
                            <FormLabel component="legend">  </FormLabel>
                            <FormGroup>
                                <FormControlLabel
                                    control={
                                        <Checkbox checked={apiStore.formData.food.traditional}
                                                  onChange={(event) => apiStore.onChangeFood(event)}
                                                  name="traditional"/>
                                    }
                                    label=""
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox checked={apiStore.formData.food.dietician}
                                                  onChange={apiStore.onChangeFood} name="dietician"/>
                                    }
                                    label=""
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox checked={apiStore.formData.food.vegan}
                                                  onChange={apiStore.onChangeFood}
                                                  name="vegan"/>
                                    }
                                    label=""
                                />
                            </FormGroup>
                        </FormControl>
                    </Box>
                    `
                </DialogContent>

                <DialogActions>
                    <Button onClick={() => apiStore.setOpenFormModal(false)}></Button>
                    <Button type="submit" onClick={apiStore.sendForm} disabled={apiStore.errorName}></Button>
                </DialogActions>
            </Dialog>

            <Dialog open={apiStore.isOpenModal} onClose={() => apiStore.setOpenModal(false)}>
                <DialogTitle></DialogTitle>
                <DialogContent>
                    <DialogContentText>{apiStore.textAuth}</DialogContentText>
                    <TextField
                        autoFocus
                        required
                        margin='dense'
                        id='login'
                        name='login'
                        label=''
                        type='text'
                        fullWidth
                        variant='standard'
                        onChange={(event) => apiStore.setReg('username', event.target.value)}
                    />
                    {apiStore.typeSign === 'register' ?
                        <TextField
                            hidden={apiStore.typeSign === 'login'}
                            required
                            margin='dense'
                            id='email'
                            name='email'
                            label='Email'
                            type='email'
                            fullWidth
                            variant='standard'
                            onChange={(event) => apiStore.setReg('email', event.target.value)}
                        />
                        : null
                    }
                    <TextField
                        required
                        margin='dense'
                        id='password'
                        name='password'
                        label=''
                        type='password'
                        fullWidth
                        variant='standard'
                        onChange={(event) => apiStore.setReg('password', event.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => apiStore.setOpenModal(false)}></Button>
                    <Button type='submit' onClick={apiStore.auth}
                        // disabled={apiStore.errorLogin || apiStore.errorPassword}
                    >
                        
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
}

export default observer(Dialogs);